























































































































import uniPopup from '../../../components/uni-popup-bank.vue';
import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue';
import mpvuePickerF from '../../../components/mpvue-picker/mpvuePicker.vue';
export default {
	data() {
		return {
			money:'',
			bank_name:'',
			bankcard_id:'',
			aisle_name:'',
			block_id:'',
			credit_name:'',
			credit_id:'',
			loading:false,
			disabled:true,
			popType: 'middle',
			showPopupBottom: false,
			radioItems:[],
			payFee:'',
			vipPayFee:'',
			is_area:0,
			zx_area:0,
			themeColor: '#007AFF',
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0],
			pickerValueArray: [],
			pickerSingleArray:[],
			province:'',
			province_name:'',
			city:'',
			city_name:'',
			pickerValueArrayF:[],
			pickerSingleArrayF:[],
		}
	},
	onLoad(e) {
		if(e.money > 0 ){
			this.money = e.money;
			this.bank_name = e.bank_name;
			this.bankcard_id = e.bankcard_id;
			this.aisle_name = e.aisle_name;
			this.block_id = e.block_id;
		}else{
			uni.switchTab({
				url: '/pages/tabBar/index/index'
			})
		}
	},
	onShow() {
		this.getPosStepData();
	},
	onHide() {
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
		}
	},
	onBackPress() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel();
			return true;
		}
		
		if (this.$refs.mpvuePickerF.showPicker) {
			this.$refs.mpvuePickerF.pickerCancel();
			return true;
		}
		
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
			return true;
		}
	},
	onUnload() {
		if (this.$refs.mpvuePicker.showPicker) {
			this.$refs.mpvuePicker.pickerCancel();
			return true;
		}
		
		if (this.$refs.mpvuePickerF.showPicker) {
			this.$refs.mpvuePickerF.pickerCancel();
			return true;
		}
		
		if (this.showPopupBottom) {
			this.showPopupBottom = false;
		}
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	methods: {
		// 单列
		showSinglePicker() {
			this.pickerValueArray = this.pickerSingleArray
			this.mode = 'selector'
			this.deepLength = 1
			this.pickerValueDefault = [0]
			this.$refs.mpvuePicker.show()
		},
		showSinglePickerCity() {
			this.pickerValueArrayF = this.pickerSingleArrayF
			this.mode = 'selector'
			this.deepLength = 1
			this.pickerValueDefault = [0]
			this.$refs.mpvuePickerF.show()
		},
		onConfirm(e) {
				this.province = e.value[0];
				this.province_name = e.label;
				this.city = '';
				this.city_name = '';
				this.getCity(e.value[0]);
		},
		onConfirmF(e) {
				this.city = e.value[0];
				this.city_name = e.label;
		},
		onCancel(e) {
			console.log(e)
		},
		switch2Change: function (e) {
			if(e.target.value){
				this.province = '';
				this.province_name = '';
				this.city = '';
				this.city_name = '';
				this.getProvince();
				this.zx_area = 1;
			}else{
				this.zx_area = 0;
			}
			
		},
		getProvince: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const block_id =  this.block_id;
			try {
				uni.showLoading();
				this.$http.post(this.websiteUrl + '/api/pay/getProvince', {
					openid,
					sessionKey,
					block_id
				}, (res) => {
					uni.hideLoading();
					if (res.data.code == 1) {
						this.pickerSingleArray = res.data.result;
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				});
			} catch (e) {
				uni.hideLoading();
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
			}
		},
		getCity: function (province) {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const block_id = this.block_id;
			try {
				uni.showLoading();
				this.$http.post(this.websiteUrl + '/api/pay/getCity', {
					openid,
					sessionKey,
					province,
					block_id,
				}, (res) => {
					uni.hideLoading();
					if (res.data.code == 1) {
						this.pickerSingleArrayF = res.data.result;
					}else{
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				});
			} catch (e) {
				uni.hideLoading();
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				});
			}
		},
		goPayMoney(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const money = this.money;
			const bankcard_id = this.bankcard_id;
			const block_id = this.block_id;
			const credit_id = this.credit_id;
			
			const province = this.province;
			const city = this.city;
			const zx_area = this.zx_area;
			if(openid == null){
				return;
			}
			const that = this;
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				this.$http.post(this.websiteUrl+'/api/pay/goPayMoney',{openid,sessionKey,money,bankcard_id,block_id,credit_id,province,city,zx_area},(res) => {
					if(res.data.code==1){
						if(res.data.result.url != ''){
							that.loading = false;
							uni.navigateTo({
								url: '/pages/card/web-view/web-view?url='+res.data.result.url
							})
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
									uni.switchTab({
										url: '/pages/tabBar/index/index'
									})
								}
							})
						}
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
							}
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false,
					success:function() {
						that.loading = false;
					}
				})
			}
		},
		getPosStepData(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const money = this.money;
			const bankcard_id = this.bankcard_id;
			const block_id = this.block_id;
			
			if(openid == null){
				return;
			}
			try {
				this.$http.post(this.websiteUrl+'/api/pay/getPosStepData',{openid,sessionKey,money,bankcard_id,block_id},(res) => {
					if(res.data.code==1){
						this.radioItems = res.data.result.creditCard;
						this.is_area = res.data.result.is_area;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		getPosFee(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const money = this.money;
			const bankcard_id = this.bankcard_id;
			const block_id = this.block_id;
			const credit_id = this.credit_id;
			if(openid == null){
				return;
			}
			try {
				this.$http.post(this.websiteUrl+'/api/pay/getPosFee',{openid,sessionKey,money,bankcard_id,block_id,credit_id},(res) => {
					if(res.data.code==1){
						this.payFee = res.data.result.payFee;
						this.vipPayFee = res.data.result.vipPayFee;
						this.disabled = false;
					}else{
						this.disabled = true;
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		radioChange: function(e) {
			var checked = e.target.value
			var changed = {}
			for (var i = 0; i < this.radioItems.length; i++) {
				if (checked.indexOf(this.radioItems[i].name) !== -1) {
					changed['radioItems[' + i + '].checked'] = true;
					this.credit_name = this.radioItems[i].value;
				} else {
					changed['radioItems[' + i + '].checked'] = false;
				}
			}
			this.credit_id = e.detail.value;
			this.showPopupBottom = false;
			this.getPosFee();
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupBottom = false;
			this.showAisleBottom = false;
		},
		//展示底部 popup
		showBottomPopup: function() {
			this.hidePopup();
			this.popType = 'bottom';
			this.showPopupBottom = true;
		},
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if(openid == null){
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
				return false;
			}
			uni.navigateTo({
				url: url
			})
		}
	},
	components: {
		uniPopup,mpvuePicker,mpvuePickerF
	}
}
